/**
 * 
 * DateUtilsformatparse
 * format(date, pattern)
 * 
 * var date = new Date();
 * DateUtils.format(date, 'yyyy-MM-dd HH:mm:ss'); //2015-08-12 13:00:00
 *
 * parse(str, pattern)
 * 
 * var str = 2015-08-12 13:00:00;
 * DateUtils.format(str, 'yyyy-MM-dd HH:mm:ss');
 *
 * parsestrDate.parse()
 *
 *          
 *   --------   ---------------------------------------------------------------
 *   yy         201515
 *   yyyy       
 *   M          1 ~ 12
 *   MM         01 ~ 120
 *   MMM        Jan
 *   MMMM       January
 *   d          1~31
 *   dd         01~310
 *   ddd        
 *   dddd       
 *   H          240~23
 *   HH         2400~230
 *   h          120~11
 *   hh         1200~110
 *   m          0~59
 *   mm         00~590
 *   s          0~59
 *   ss         00~590
 *   S          0~999
 *   SS         00~9990
 *   SSS        000~9990
 *   t          
 *   tt         
 *   --------   ---------------------------------------------------------------
 *
 * @author accountwcx@qq.com
 * @date   2015-08-12
 */
const DateUtils = (function() {
    /*
      const locale = {
          dayNames: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
          shortDayNames: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
          monthNames: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"],
          shortMonthNames: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
          am: 'AM',
          pm: 'PM',
          shortAm: 'A',
          shortPm: 'P'
      };
      */

    const locale = {
        dayNames: [
            "",
            "",
            "",
            "",
            "",
            "",
            ""
        ],
        shortDayNames: ["", "", "", "", "", "", ""],
        monthNames: [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
        ],
        shortMonthNames: [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
        ],
        am: "",
        pm: "",
        shortAm: "",
        shortPm: ""
    };

    /**
     * 0
     */
    function leftPad(str, size) {
        let result = "" + str;

        while (result.length < size) {
            result = "0" + result;
        }

        return result;
    }

    var parseToken = (function() {

        const match2 = /\d{2}/, // 00 - 99
        //match3 = /\d{3}/,          // 000 - 999
        match4 = /\d{4}/, // 0000 - 9999
        match1to2 = /\d{1,2}/, // 0 - 99
        match1to3 = /\d{1,3}/, // 0 - 999
        //match1to4 = /\d{1,4}/,     // 0 - 9999
        match2w = /.{2}/, // 
        match1wto2w = /.{1,2}/, // 1~2
        map = {
            //
            yy: {
                regex: match2,
                name: "year"
            },
            //
            yyyy: {
                regex: match4,
                name: "year"
            },
            //0
            MM: {
                regex: match2,
                name: "month"
            },
            //
            M: {
                regex: match1to2,
                name: "month"
            },
            //0
            dd: {
                regex: match2,
                name: "date"
            },
            //
            d: {
                regex: match1to2,
                name: "date"
            },
            //24
            HH: {
                regex: match2,
                name: "hours"
            },
            //24
            H: {
                regex: match1to2,
                name: "hours"
            },
            //12
            hh: {
                regex: match2,
                name: "hours"
            },
            //12
            h: {
                regex: match1to2,
                name: "hours"
            },
            //
            mm: {
                regex: match2,
                name: "minutes"
            },
            //
            m: {
                regex: match1to2,
                name: "minutes"
            },
            s: {
                regex: match1to2,
                name: "seconds"
            },
            ss: {
                regex: match2,
                name: "seconds"
            },
            //
            tt: {
                regex: match2w,
                name: "t"
            },
            //
            t: {
                regex: match1wto2w,
                name: "t"
            },
            //
            S: {
                regex: match1to3,
                name: "millisecond"
            },
            //
            SS: {
                regex: match1to3,
                name: "millisecond"
            },
            //
            SSS: {
                regex: match1to3,
                name: "millisecond"
            }
        };

        return function(token, str, dateObj) {

            let result;

            const part = map[token];

            if (part) {
                result = str.match(part.regex);
                if (result) {
                    dateObj[part.name] = result[0];
                    return result[0];
                }
            }

            return null;
        };
    })();

    return {
        locale: locale,

        format: function(val, pattern) {
            if (Object.prototype.toString.call(val) !== "[object Date]") {
                return "";
            }

            if (
            Object.prototype.toString.call(pattern) !== "[object String]" ||
            pattern === ""
            ) {
                pattern = "yyyy-MM-dd HH:mm:ss";
            }

            const fullYear = val.getFullYear(),
            month = val.getMonth(),
            day = val.getDay(),
            date = val.getDate(),
            hours = val.getHours(),
            minutes = val.getMinutes(),
            seconds = val.getSeconds(),
            milliseconds = val.getMilliseconds();

            return pattern.replace(
            /(\\)?(dd?d?d?|MM?M?M?|yy?y?y?|hh?|HH?|mm?|ss?|tt?|SS?S?)/g,
            function(m) {
                if (m.charAt(0) === "\\") {
                    return m.replace("\\", "");
                }

                const locale = DateUtils.locale;

                switch (m) {
                    case "hh":
                        return leftPad(
                        hours < 13 ? (hours === 0 ? 12 : hours) : hours - 12,
                        2
                        );
                    case "h":
                        return hours < 13 ? (hours === 0 ? 12 : hours) : hours - 12;
                    case "HH":
                        return leftPad(hours, 2);
                    case "H":
                        return hours;
                    case "mm":
                        return leftPad(minutes, 2);
                    case "m":
                        return minutes;
                    case "ss":
                        return leftPad(seconds, 2);
                    case "s":
                        return seconds;
                    case "yyyy":
                        return fullYear;
                    case "yy":
                        return (fullYear + "").substring(2);
                    case "dddd":
                        return locale.dayNames[day];
                    case "ddd":
                        return locale.shortDayNames[day];
                    case "dd":
                        return leftPad(date, 2);
                    case "d":
                        return date;
                    case "MMMM":
                        return locale.monthNames[month];
                    case "MMM":
                        return locale.shortMonthNames[month];
                    case "MM":
                        return leftPad(month + 1, 2);
                    case "M":
                        return month + 1;
                    case "t":
                        return hours < 12 ? locale.shortAm : locale.shortPm;
                    case "tt":
                        return hours < 12 ? locale.am : locale.pm;
                    case "S":
                        return milliseconds;
                    case "SS":
                        return leftPad(milliseconds, 2);
                    case "SSS":
                        return leftPad(milliseconds, 3);
                    default:
                        return m;
                }
            }
            );
        },

        parse: function(val, pattern) {
            if (!val) {
                return null;
            }

            if (Object.prototype.toString.call(val) === "[object Date]") {
                // val
                return val;
            }

            if (Object.prototype.toString.call(val) !== "[object String]") {
                // val
                return null;
            }

            let time;
            if (
            Object.prototype.toString.call(pattern) !== "[object String]" ||
            pattern === ""
            ) {
                // fmt
                // 
                time = Date.parse(val);
                if (isNaN(time)) {
                    return null;
                }

                return new Date(time);
            }

            let i,
            token,
            tmpVal;

            const tokens = pattern.match(
            /(\\)?(dd?|MM?|yy?y?y?|hh?|HH?|mm?|ss?|tt?|SS?S?|.)/g
            );

            const dateObj = {
                year: 0,
                month: 1,
                date: 0,
                hours: 0,
                minutes: 0,
                seconds: 0,
                millisecond: 0
            };

            for (i = 0; i < tokens.length; i++) {
                token = tokens[i];
                tmpVal = parseToken(token, val, dateObj);

                if (tmpVal !== null) {
                    if (val.length > tmpVal.length) {
                        val = val.substring(tmpVal.length);
                    } else {
                        val = "";
                    }
                } else {
                    val = val.substring(token.length);
                }
            }

            if (dateObj.t) {
                if (
                DateUtils.locale.pm === dateObj.t ||
                DateUtils.locale.shortPm === dateObj.t
                ) {
                    dateObj.hours = +dateObj.hours + 12;
                }
            }

            dateObj.month -= 1;

            return new Date(
            dateObj.year,
            dateObj.month,
            dateObj.date,
            dateObj.hours,
            dateObj.minutes,
            dateObj.seconds,
            dateObj.millisecond
            );
        }
    };

})();

export default DateUtils;
